/* Animated Circles CSS */

.circles {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.circles li {
  position: absolute;
  display: block;
  list-style: none;

  -webkit-animation: animate 10s linear infinite;

          animation: animate 10s linear infinite;
}

.circles li:nth-child(1) {
  left: 40%;
  top: 32vh;
  width: 40px;
  height: 40px;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-animation-duration: 10s;
          animation-duration: 10s;
}

.pagination-link {
  cursor: pointer;
}

@-webkit-keyframes animate {
  0% {
    -webkit-transform: translateY(0) rotate(50deg);
            transform: translateY(0) rotate(50deg);
    opacity: 0;
  }
  50% {
    -webkit-transform: translateX(20vh) rotate(50deg);
            transform: translateX(20vh) rotate(50deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(40vh) rotate(50deg);
            transform: translateX(40vh) rotate(50deg);
    opacity: 0;
  }
}

@keyframes animate {
  0% {
    -webkit-transform: translateY(0) rotate(50deg);
            transform: translateY(0) rotate(50deg);
    opacity: 0;
  }
  50% {
    -webkit-transform: translateX(20vh) rotate(50deg);
            transform: translateX(20vh) rotate(50deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(40vh) rotate(50deg);
            transform: translateX(40vh) rotate(50deg);
    opacity: 0;
  }
}

.pointer-link {
  cursor: pointer;
}

.fa-trash:hover {
  font-size: large;
}

